import React from 'react';
import "./Contact.css";
import Spinner from '../../Spinner/Spinner';
const contact = (props) => {
    let contact = (<div className="Contact">
        <div className="imageContact"></div>
        <div className="Contactinfo">
            <div className="col-wise">
                <h2><pre style={{ margin: 1 }}>{props.name}</pre></h2>
                <h3>{props.mobile}</h3>
            </div>
            <div className="row-wise">
                <button onClick={props.editFunc}>Edit</button>
                <button onClick={props.deleteContact}>Delete</button>
            </div>
        </div>
    </div>)
    if (props.edit && props.edit.id === props.index) {
        contact = (
            <div className="Contact">
                <div className="imageContact"></div>
                <div className="Contactinfo">
                    <div className="col-wise">
                        <form onSubmit={props.submitEditRequest}>
                            <input type="text" value={props.edit.editName} name="editName" onChange={props.editFormChanger} className="editName" placeholder="Name" /><br />
                            <input type="text" value={props.edit.editMobile} name="editMobile" onChange={props.editFormChanger} className="editMobile" placeholder="Mobile" />
                        </form>
                    </div>
                    <div className="row-wise">
                        <button onClick={props.submitEditRequest}>Submit</button>
                        <button onClick={props.cancelEdit}>Cancel</button>

                    </div>
                </div>
            </div>
        );
        if (props.loadEdit) {
            contact = (
                <div className="Contact">
                    <div className="imageContact"></div>
                    <div className="Contactinfo">
                        <div className="col-wise loading">
                        <Spinner cls="small"/>
                        </div>
                    </div>
                </div>
            )
        }
    }
    return contact;
};
export default contact;


