import React, { Component } from "react";
import ContactList from "../../components/UI/ContactList/ContactList";
import AddContact from "../../components/UI/AddContact/AddContact";
import gql from "graphql-tag";

import { graphql, compose } from 'react-apollo';

import "./Contacts.css";
import Spinner from "../../components/UI/Spinner/Spinner";
class Contacts extends Component {
    state = {
        load: false,
        name: '',
        mobile: '',
        edit: null,
        loadContact: false
    }
    componentDidMount() {
        this.props.visitInc({
            variables: {}
        }).then(res => {

        }).catch(err => {
            console.log(err);
        })
    }
    onContactAddHandler = (event) => {
        event.preventDefault();
        if (this.state.name.trim() === '' || this.state.mobile.trim() === '') {
            return;
        }
        this.setState({ load: true });
        this.props.addContact({
            variables: {
                username: this.state.name,
                contact: this.state.mobile
            }
        }).then(res => {
            this.setState({
                name: '',
                mobile: '',
            })
            this.props.showContact.refetch().then(resolve => {
                this.setState({ load: false })
            });;
        })
            .catch(err => {
                this.setState({ load: false })

            })



    }
    onDeleteContactHandler = (id) => {
        this.setState({ load: true })

        this.props.deleteContact({
            variables: {
                id: id
            }
        }).then(res => {
            this.props.showContact.refetch().then(resolve => {
                this.setState({ load: false })
            });

        }).catch(err => {
            this.setState({ load: false })

        })

    }

    formChanger = (event) => {
        this.setState({
            [event.target.name]: event.target.value,
        })

    }
    onEditContactHandler = (id) => {
        const selected = this.props.showContact.contacts.find((c) => (c.id === id));
        this.setState({
            edit: {
                id: id,
                editName: selected.username,
                editMobile: selected.contact
            }
        })

    }
    onEditFormChangeHandler = (event) => {

        if (this.state.edit) {
            this.setState({
                edit: {
                    ...this.state.edit,
                    [event.target.name]: event.target.value
                }
            })
        }
    }
    onEditRequestHandler = (event) => {
        event.preventDefault();
        this.setState({ loadContact: true })
        this.props.editContact({
            variables: {
                id: this.state.edit.id,
                username: this.state.edit.editName,
                contact: this.state.edit.editMobile
            }
        }).then(res => {
            this.props.showContact.refetch().then(resolve => {
                this.setState({ loadContact: false, edit: null })
            });

        }).catch(err => {
        })
    }
    onCancelEditHandler = () => {
        this.setState({
            edit: null
        })
    }
    render() {

        let contactList = (<ContactList
            contacts={this.props.showContact.contacts}
            deleteContact={this.onDeleteContactHandler}
            editFunc={this.onEditContactHandler}
            edit={this.state.edit}
            editFormChanger={this.onEditFormChangeHandler}
            submitEditRequest={this.onEditRequestHandler}
            cancelEdit={this.onCancelEditHandler}
            loadEdit={this.state.loadContact}
        />);
        if (this.props.showContact.loading || this.state.load) {
            contactList = (<Spinner />)
        }

        return (
            <div className="Contacts">
                <AddContact name={this.state.name} mobile={this.state.mobile} contactadd={this.onContactAddHandler} formChanger={this.formChanger} />
                {contactList}
                <div className="count">
                    {this.props.visitors.loading ? <div>Loading</div> : <label>Count: <span>{this.props.visitors.visits.length}</span></label>}
                </div>
            </div>

        )
    }
}

const showContacts = gql`
{
  contacts {
    id,
    username,
    contact
  }
}
`;
const visitors = gql`
{
    visits{
        id
    }
}
`;
const visitInc = gql`
mutation{
  vistInc {
    id
  }
}
`;
const addContact = gql`
mutation addContact($username: String!,$contact: String!){
    addContact(username:$username,contact:$contact){
        id,
        username,
        contact
    }
}
`;
const deleteContact = gql`
    mutation deleteContact($id:ID!){
        deleteContact(id:$id){
            id
        }
    }
`;
const editContact = gql`
    mutation editContact($id:ID!,$username:String,$contact:String){
        editContact(id:$id,username:$username,contact:$contact){
            id
        }
    }
`;
const ComponentWithMutationsAndQueries = compose(
    graphql(showContacts, {
        name: 'showContact'
    }),
    graphql(addContact, {
        name: 'addContact'
    }),
    graphql(visitors, {
        name: 'visitors'
    }),
    graphql(visitInc, {
        name: 'visitInc'
    })
    ,
    graphql(deleteContact, {
        name: 'deleteContact'
    }),
    graphql(editContact, {
        name: 'editContact'
    })
)(Contacts);
export default ComponentWithMutationsAndQueries;


