import React from 'react';
import "./AddContact.css";
const addContact = (props) => {
return(
    <div className="AddContact">
        <form onSubmit={props.contactadd}>
            <input type="text" name="name" value={props.name} onChange={props.formChanger}/>
            <input type="text" name="mobile" value={props.mobile} onChange={props.formChanger}/>
            <input type="submit" value="Add Contact"/>

        </form>
    </div>
);
};
export default addContact;


