import React from 'react';
import Contact from './Contact/Contact';
import "./ContactList.css";

const contactList = (props) => {
    const contacts=props.contacts.map((contact,index)=>(<Contact name={contact.username} 
        key={contact.id} 
        mobile={contact.contact} 
        deleteContact={() => props.deleteContact(contact.id)}
        editFunc={()=>props.editFunc(contact.id)}
        edit={props.edit}
        index={contact.id}
        editFormChanger={props.editFormChanger}
        submitEditRequest={props.submitEditRequest}
        cancelEdit={props.cancelEdit}
        loadEdit={props.loadEdit}
        />))
    return (
        <div className="ContactList">
            <div style={{ display: "grid", width: "90%", gridTemplateColumns: "repeat( auto-fit, minmax(290px, 1fr) )"}}>
              {contacts}
            </div>
        </div>
    );
};
export default contactList;


